








































































// 数字角标样式外联
// import uniBadge from '@/components/uni-badge/uni-badge.vue'
export default {
	components: {},
	data() {
		return {
			orderSubject: [],  //订单主体	
			checkNum : 0,
			items:[{
				value:'red',
				name:'全部',
				typeId:"-1"
			},
			{
				value:'red',
				name:'未支付',
				typeId:"0"
			},
			{
				value:'red',
				name:'已支付',
				typeId:"1"
			},
			{
				value:'red',
				name:'已完成',
				typeId:"2"
			},
			{
				value:'red',
				name:'已退款',
				typeId:"3"
			}]
		};
	},
	methods: {
		// 点击切换类别
		Whole(index){
			this.checkNum = index;
			var typeid = this.items[index].typeId;  //typeid  状态类别id
			var wulala=uni.getStorageSync("jsonList");  //获取存储在Storage里的值
			var data = JSON.parse(wulala); //JSON字符串转对象
			var Signboard = data.Ident_Signboard;  //给Signboard 赋值
			var Signguid = data.Ident_Signguid;  //给Signguid 赋值
			if(typeid == -1){
				uni.request({
					url: 'http://192.168.199.194/mobile/order/OwnOrder',
					method: 'GET',
					data:{
						Ident_Signboard: Signboard,
						Ident_Signguid: Signguid
					},
					success: (res) => {
						if(res.data.status == 0){
							console.log(res)
							this.orderSubject = res.data.order;  //给orderSubject 订单主体赋值
						}
					}
				})
			}else {
				uni.request({
					url: 'http://192.168.199.194/mobile/order/orderTypes',
					method: 'GET',
					data: {
						Ident_Signboard: Signboard,
						Ident_Signguid: Signguid,
						typesid: typeid
					},
					success: (res) => {
						if(res.data.status == 0){
							this.orderSubject = res.data.order;  //给orderSubject 订单主体赋值
						}
					}
				})
			}
		},
		orderTap(e){
			console.log(e)
			uni.navigateTo({
				url: 'details?orderid='+e
			})
		}
	},
	onLoad: function (){
		var wulala=uni.getStorageSync("jsonList");  //获取存储在Storage里的值
		var datas= JSON.parse(wulala);//JSON字符串转对象
		var Signboard = datas.Ident_Signboard;  //给Signboard 赋值
		var Signguid = datas.Ident_Signguid;  //给Signguid 赋值
		
		uni.request({
			url: 'http://192.168.199.194/mobile/order/OwnOrder',
			data: {
				Ident_Signboard: Signboard,
				Ident_Signguid: Signguid
			},
			method: 'GET',
			success: res => {
				if(res.data.status == 0){
					console.log(res)
					this.orderSubject = res.data.order;  //给orderSubject 订单主体赋值
				}
			}
		})
	}
};
